@import url(https://fonts.googleapis.com/css?family=Righteous|Roboto+Slab&display=swap);
@import url(https://fonts.googleapis.com/css?family=Permanent+Marker&display=swap);
:root {
  --c-primary:#1E5C62;
  --c-secondary: #86E2D5;
  --c-accent-lime: #C9F227;
  --c-accent-purple: #B200FD;
  --text-light: #D2D7D3;
  /* Gray Scale */
  --c-white: #ffffff;
  --c-gray-98: #f3f3f3;
  --c-gray-95: #EDEDED;
  --c-gray-90: #E3E3E3;
  --c-gray-85: #D9D9D9;
  --c-gray-80: #CCCCCC;
  --c-gray-75: #C0C0C0;
  --c-gray-70: #B0B0B0;
  --c-gray-60: #999999;
  --c-gray-50: #808080;
  --c-gray-45: #727272;
  --c-gray-40: #696969;
  --c-gray-30: #4D4D4D;
  --c-gray-20: #363636;
  --c-gray-15: #222222;
  --c-gray-10: #171717;
  --c-black: #000000; }

:root {
  /* font size */
  --fontSize-sm: 0.5rem;
  --fontSize-normal: 1rem;
  --fontSize-lg: 1.5rem;
  --fontSize-xl: 2rem;
  /* font face */
  --heading-font: "Righteous", cursive;
  --body-font: "Roboto Slab", serif;
  --hand-font: "Permanent Marker", cursive;
  /* font weight */
  --fontWeight-normal: 400;
  --fontWeight-bold: 700; }

body {
  background: #f3f3f3;
  background: var(--c-gray-98);
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

.Nav-svg {
  fill: var(--c-accent-purple);
  height: calc(var(--fontSize-xl) * 1.5);
  width: calc(var(--fontSize-xl) * 1.5);
}

.Nav-svg:hover {
  -webkit-transform: scale(1.5);
          transform: scale(1.5);
}

.About {
  -webkit-transform: rotate(-25deg);
          transform: rotate(-25deg);
}

.Resume {
  -webkit-transform: rotate(9deg);
          transform: rotate(9deg);
}

.Contact {
  -webkit-transform: rotate(-19deg);
          transform: rotate(-19deg);
}

.Meetup-container {
  align-items: center;
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.Meetup-container > h1 {
  margin: 0 1.25rem;
}

@media (max-width: 1024px) {
  .Meetup-container > h1 {
    font-size: calc(var(--fontSize-lg) * 0.9);
    margin: 0 1rem;
  }
}

.Header {
  background: var(--c-gray-98);
  align-items: flex-end;
  display: flex;
  flex-direction: row;
  height: 14rem;
  margin-bottom: 1.45rem;
  padding: 0;
  position: fixed;
  right: 0;
  top: 0;
  width: 100%; }
  .Header > nav {
    display: flex;
    flex-direction: row;
    justify-content: center;
    margin-left: 2rem; }
  @media (max-width: 675px) {
    .Header > nav {
      display: flex;
      flex-direction: column;
      margin-left: 2rem; } }
  .Header__nav-link {
    margin: 0 0.5rem;
    text-decoration: none; }
    .Header__nav-link > h2 {
      font-size: 0;
      margin: 0;
      padding: 0;
      visibility: hidden; }
  .Header__title {
    background: var(--c-gray-90);
    border-bottom: 1rem solid var(--c-primary);
    border-radius: 1rem;
    height: 9rem;
    left: 1rem;
    position: relative;
    top: -2rem;
    width: 15rem;
    -webkit-transform: rotate(-15deg);
            transform: rotate(-15deg); }
    .Header__title-hello {
      background: var(--c-primary);
      border-radius: 1rem 1rem 0 0;
      position: absolute;
      width: 100%; }
      .Header__title-hello > h1 {
        color: var(--text-light);
        letter-spacing: 1rem;
        margin: 0;
        text-align: center; }
      .Header__title-hello > p {
        color: var(--text-light);
        font-family: var(--body-font);
        margin: 0;
        text-align: center; }
    .Header__title-heading {
      position: relative;
      left: 3rem;
      top: 4.3rem; }
      .Header__title-heading > a {
        color: var(--c-gray-10);
        font-family: var(--hand-font);
        font-size: calc(var(--fontSize-xl) * 1.5);
        letter-spacing: 0.1rem;
        position: absolute;
        right: 8rem;
        top: -0.75em;
        text-decoration: none; }

.AboutPage {
  display: flex;
  flex-direction: row;
  height: 20rem;
  justify-content: center;
  margin-top: 15rem;
  width: 100%; }

.AboutPage__card {
  flex-grow: 1;
  max-width: 50%;
  padding: 2rem; }

.AboutPage__card > a {
  color: var(--c-accent-purple);
  text-decoration: none; }

.AboutPage__card-text {
  font-family: var(--body-font);
  font-size: var(--fontSize-normal); }

@media (max-width: 1024px) {
  .AboutPage {
    margin-top: 13rem; }
  .AboutPage__card {
    max-width: 80%; }
  .AboutPage__card-text {
    font-family: var(--body-font);
    font-size: var(--fontSize-normal); } }

.Hero {
  display: flex;
  flex-direction: row;
  height: 20rem;
  justify-content: center;
  margin-top: 15rem;
  width: 100%; }

.Hero__card {
  flex-grow: 1;
  max-width: 50%;
  padding: 2rem; }

.Hero__card-text {
  font-family: var(--body-font);
  font-size: var(--fontSize-lg); }

@media (max-width: 1024px) {
  .Hero {
    margin-top: 13rem; }
  .Hero__card {
    max-width: 80%; }
  .Hero__card-text {
    font-family: var(--body-font);
    font-size: var(--fontSize-normal); } }

.ContactPage {
  display: flex;
  flex-direction: row;
  height: 20rem;
  justify-content: center;
  margin-top: 15rem;
  width: 100%; }

.ContactPage__card {
  flex-grow: 1;
  max-width: 50%;
  padding: 2rem; }

h1.ContactPage__card-text {
  font-size: var(--fontSize-xl); }

h2.ContactPage__card-text {
  font-size: var(--fontSize-lg);
  padding: 2rem;
  text-align: center;
  text-transform: capitalize; }

.ContactPage__card-text > a {
  color: var(--c-primary);
  text-align: center;
  text-decoration: none; }

.ContactPage__card-social {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  width: 100%; }

.ContactPage__card-social > a {
  margin: 0.5rem; }

.ContactPage__card-text {
  font-family: var(--body-font);
  font-size: var(--fontSize-lg); }

@media (max-width: 1024px) {
  .ContactPage {
    margin-top: 13rem; }
  .ContactPage__card {
    max-width: 80%; }
  h1.ContactPage__card-text {
    font-size: calc(var(--fontSize-xl) * .7); }
  h2.ContactPage__card-text {
    font-size: calc(var(--fontSize-lg) * .7);
    padding: 2rem;
    text-align: center;
    text-transform: capitalize; }
  .ContactPage__card-social {
    margin: 0 auto;
    width: 80%; } }

.ResumePage {
  display: flex;
  flex-direction: row;
  height: 20rem;
  justify-content: center;
  margin-top: 15rem;
  width: 100%; }

.ResumePage__card {
  flex-grow: 1;
  max-width: 50%;
  padding: 2rem; }

h1.ResumePage__card-text {
  font-family: var(--body-font);
  font-size: var(--fontSize-lg);
  font-weight: var(--fontWeight-bold); }

@media (max-width: 1024px) {
  .ResumePage {
    margin-top: 13rem; }
  .ResumePage__card {
    max-width: 80%; }
  h1.ResumePage__card-text {
    font-family: var(--body-font);
    font-size: var(--fontSize-lg);
    font-weight: var(--fontWeight-bold); } }

.App {
  background: var(--c-gray-98);
  text-align: center; }

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none; }

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white; }

.App-link {
  color: #61dafb; }

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

